.stand {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    margin-top: 64px;
    margin-bottom: 55px;
}

.inputs {
    display: flex;
    flex-direction: column;
    gap: 48px;
}

.buttons {
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-top: 2px;

    transform: translateX(-50%);
}

.checkboxes {
    display: flex;
    flex-direction: column;
    gap: 36px;
    margin-top: 15px;
}